import {Box} from "@mui/material";
import {RoundedStick} from "../RoundedStick";
import {InfoBlock} from "../InfoBlock";
import React, {LegacyRef} from "react";

interface Props {
    type: 'ABOUT US' | 'WHY ME' | 'CASES';
    someRef: LegacyRef<HTMLDivElement> | undefined;
}

export type headline = 'ABOUT US' | 'WHY ME' | 'CASES';

const TYPE_TEXT: Record<headline, string> = {
    "ABOUT US": ' ',
    "WHY ME": ' ?',
    "CASES": ' ',
};

export const BodyBlock: React.FC<Props> = ({type, someRef}) => {
    return (
    <div ref={someRef} style={{color: 'white', backgroundColor: 'black',
    paddingBottom: type === 'CASES' ? 4 : 0  }}>
      <Box sx={{ paddingTop: '32px', width: '80%', marginLeft: '10%' }}>
        <Box sx={{
            backgroundClip: 'content-box',
            width: 'fit-content',
            fontSize: { xs: 36, sm: 54, md: 72 },
            lineHeight: { xs: '46px', sm: '72px' },
            alignItems: 'start',
            display: 'flex',
            fontWeight: 800,
            flexDirection: 'column',
            justifyContent: 'start',
        }}>
            {TYPE_TEXT[type]}
            <RoundedStick mb={'24px'} mt={'12px'} width={'100%'} align={'start'} h={18} />
        </Box>
        <InfoBlock type={type} />
      </Box>
    </div>
  )
};