import {Box} from "@mui/material";
import React from "react";

interface Props {
    mb: string;
    mt: string;
    ml?: string;
    h: number;
    width: string;
    align: string;
    color?: string;
}

export const RoundedStick: React.FC<Props> = ({
    mb, mt, h, width, align, ml, color
}) => {
    return (
        <Box sx={{
            marginLeft: ml ? ml : 0,
            backgroundColor: color ? color : '#c09fff',
            borderRadius: '26px',
            alignSelf: align,
            marginBottom: mb,
            marginTop: mt,
            width: width,
            height: {
                xs: h * 0.56,
                sm: h * 0.78,
                md: h,
            }}}
        />
    );
}