import React, {useCallback, useState, useEffect} from 'react';
import {useRef} from 'react';
import {BodyBlock} from "./components/BodyBlock";
import {Box} from "@mui/material";
import {Lines} from "./components/Lines";
import {NavigationBlock} from "./components/NavigationBlock";
import four from './components/images/four.svg';
import arrow from './components/images/arrow.svg';
import {RoundedStick} from "./components/RoundedStick";

function App() {
    const aboutRef = useRef<null | HTMLDivElement>(null);
    const whyRef = useRef<null | HTMLDivElement>(null);
    const casesRef = useRef<null | HTMLDivElement>(null);
    const [showScrollButton, setShowScrollButton] = useState(false);
    useEffect(() => {
        const handleScrollButtonVisibility = () => {
            window.pageYOffset > 300 ? setShowScrollButton(true) : setShowScrollButton(false);
        }
        window.addEventListener('scroll', handleScrollButtonVisibility);
        return () => {
            window.removeEventListener('scroll', handleScrollButtonVisibility);
        }
    }, []);
    const handleScrollToTop = useCallback(() => {
        window.scrollTo({top: 0, behavior: 'smooth'});
    }, []);
    const handleAboutClick = useCallback(() => {
        console.log(aboutRef);
        console.log(aboutRef.current);
        aboutRef.current?.scrollIntoView({behavior: 'smooth'});
    }, [aboutRef]);
    const handleWhyMeClick = useCallback(() => {
        whyRef.current?.scrollIntoView({behavior: 'smooth'});
    }, [whyRef]);
    const handleCasesClick = useCallback(() => {
        casesRef.current?.scrollIntoView({behavior: 'smooth'});
    }, [casesRef]);
    return (
     <div className="App" style={{ marginBottom: '36px' }}>
         {showScrollButton &&
             <img
                 onClick={handleScrollToTop}
                 style={{
                     zIndex: '3000',
                     width: '32px',
                     height: '32px',
                     position: 'fixed',
                     right: '60px',
                     bottom: '60px',
                     color: 'white',
                     cursor: 'pointer'
                 }}
                 src={arrow}
                 alt={"arrow"}
             />
         }
     <div style={{ overflow: 'hidden' }}>
         <Box sx={{
             marginLeft: -2,
             marginRight: -2,
             paddingTop: {xs: '30px', sm: '40px', md: '50px'},
             height: {xs: '100px', sm: '130px', md: '160px'},
         }}>
             <Lines color={'#c9fa22'} degree={0}/>
             <Lines color={'#c09fff'} degree={-7}/>
         </Box>
     </div>
     <Box sx={{
         height: 'auto',
         fontSize: { xs: 22, sm: 30, md: 38 },
         alignItems: 'center',
         display: 'flex',
         flexDirection: 'column',
         gap: '12px',
         letterSpacing: '0.05em',
         justifyContent: 'center',
         fontWeight: 800,
         backgroundColor: 'black',
         color: 'white',
     }}>
         <div>  </div>
         <div style={{ width: 'auto', textAlign: 'center', color: '#c9fa22' }}> </div>
     </Box>
     <Box sx={{ width: '36%', marginLeft: '32%', marginY: '24px' }}>
         <img style={{ width: '100%' }} src={four} alt="main" />
     </Box>
     <RoundedStick
         mb={'20px'}
         mt={'20px'}
         ml={'20%'}
         width={'60%'}
         align={'center'}
         h={10} />
     <NavigationBlock
         aboutClick={handleAboutClick}
         whyClick={handleWhyMeClick}
         casesClick={handleCasesClick}
     />
     <BodyBlock type='ABOUT US' someRef={aboutRef} />
     <BodyBlock type='WHY ME' someRef={whyRef} />
     <BodyBlock type='CASES' someRef={casesRef}/>
    </div>
    );
}

export default App;
