import {Box, List, ListItem, ListItemIcon, Typography} from "@mui/material";
import {RoundedStick} from "../RoundedStick";
import AutoAwesomeIcon from "@mui/icons-material/AutoAwesome";
import ThumbDownAltIcon from '@mui/icons-material/ThumbDownAlt';
import React from "react";
import {RoundedHeadline} from "../RoundedHeadline";

interface Props {
    iconName: string;
    iconColor: string;
    headlineText: string;
    listText: string[];
    headlineRounded: boolean;
    subText?: Record<number, string>;
    roundedHeadlineBgColor?: string;
    roundedHeadlineTextColor?: string;
    roundedHeadlineTextShadowColor?: string;
}

export const CaseListWithHeadline: React.FC<Props> = ({
    iconName,
    iconColor,
    headlineText,
    listText,
    subText,
    headlineRounded,
    roundedHeadlineTextColor,
    roundedHeadlineTextShadowColor,
    roundedHeadlineBgColor
}) => {
    return (
        <>
            {!headlineRounded ?
                <Box sx={{
                    fontFamily: 'Neutral Face',
                    fontWeight: 600,
                    fontSize: { xs: 20, sm: 28, md: 36 },
                    width: 'fit-content',
                    alignItems: 'start',
                }}>
                    {headlineText}
                    <RoundedStick mb={'10px'} mt={'12px'} width={'100%'} align={'start'} h={10} color={'#c9fa22'} />
                </Box>
                :
               <RoundedHeadline
                   value={headlineText}
                   bgColor={roundedHeadlineBgColor || '#c09fff'}
                   textColor={roundedHeadlineTextColor || 'white'}
                   textShadowColor={roundedHeadlineTextShadowColor || 'rgba(0, 0, 0, 0.8)'}
               />
            }
            <List sx={{ marginBottom: { xs: '8px', sm: '12px', md: '16px' }, paddingLeft: 0 }}>
                {listText.map((value, index) => {
                    return (
                        <>
                        <ListItem sx={{ paddingLeft: 0, alignItems: 'center' }}>
                            <ListItemIcon>
                                {iconName === 'AutoAwesomeIcon' ?
                                    <AutoAwesomeIcon sx={{ color: iconColor, width: '32px', height: '32px'  }} />
                                    :
                                    <ThumbDownAltIcon  sx={{ color: iconColor, width: '32px', height: '32px'  }} />
                                }
                            </ListItemIcon>
                            <Typography sx={{
                                fontFamily: 'Neutral Face',
                                fontWeight: 600,
                                fontSize: { xs: 16, sm: 20, md: 24 },
                            }}>
                                {value}
                                {subText && subText[index] && (
                                    <Typography sx={{
                                        fontFamily: 'Neutral Face',
                                        fontWeight: 400,
                                        fontSize: { xs: 8, sm: 10, md: 12 },
                                    }}>
                                        {subText[index]}
                                    </Typography>
                                )}
                            </Typography>
                        </ListItem>
                        </>
                    );
                })}
            </List>
        </>
    )
}