import {Box} from "@mui/material";
import React from "react";

interface Props {
    value: string;
    textColor: string;
    textShadowColor: string;
    bgColor: string;
    mb?: string;
}

export const RoundedHeadline: React.FC<Props> = ({
    value, bgColor, textShadowColor, textColor, mb
}) => {
    return (
        <Box sx={{
            fontFamily: 'Neutral Face',
            fontWeight: 600,
            fontSize: { xs: 12, sm: 20, md: 28 },
            width: 'fit-content',
            paddingX: '16px',
            paddingY: '12px',
            marginBottom: mb ? mb : 0,
            color: textColor,
            textShadow: `2px 2px 4px ${textShadowColor}`,
            backgroundColor: bgColor,
            borderRadius: '26px',
        }}>
            {value}
        </Box>
    );
};